<template>
  <div class="Top10">
    <AppHeader>
      <p slot="center">top10</p>
    </AppHeader>

    <div class="form-page">
      <div class="filters">
        <div class="filters-label">搜索范围:</div>
        <div class="date-filters">
          <input
            class="text-left"
            type="text"
            :value="rangePickerValue"
            @click="rangePicker = true"
            placeholder="请选择"
            readonly
          />
          <van-popup v-model="rangePicker" position="bottom">
            <van-picker
              show-toolbar
              :columns="rangeList"
              item-height="36"
              visible-item-count="6"
              :default-index="rangeSelectedIndex"
              @cancel="rangePicker = false"
              @confirm="rangePickerChange"
            />
          </van-popup>
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">搜索内容:</div>
        <div class="date-filters">
          <van-field
            v-model="searchKey"
            right-icon="search"
            @click-right-icon="searchFun()"
            placeholder="请输入关键字"
          />
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">条件:</div>
        <div class="date-filters">
          <div class="contrast">
            <van-tabs v-model="salesActive" @click="salesTabClick">
              <van-tab title="销售额"></van-tab>
              <van-tab title="销售数"></van-tab>
              <van-tab title="毛利额"></van-tab>
            </van-tabs>
          </div>
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">周期:</div>
        <div class="date-filters">
          <div class="contrast">
            <van-tabs v-model="cycleActive" @click="cycleTabClick">
              <van-tab title="近1天"></van-tab>
              <van-tab title="近1周"></van-tab>
              <van-tab title="近1月"></van-tab>
            </van-tabs>
          </div>
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">排序:</div>
        <div class="date-filters">
          <div class="contrast">
            <van-tabs v-model="sortActive" @click="sortTabClick">
              <van-tab title="前10"></van-tab>
              <van-tab title="末10"></van-tab>
            </van-tabs>
          </div>
        </div>
      </div>
    </div>

    <div class="detailed-table" v-if="hasData">
      <div class="table-content">
        <table>
          <thead>
            <tr>
              <th>序号</th>
              <th>商品代码</th>
              <th>商品名称</th>
              <th>第二代码</th>
              <th>销售额</th>
              <th>小类代码</th>
              <th>品牌</th>
              <th>供应商</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in resultData" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ items.code }}</td>
              <td>{{ items.goodname }}</td>
              <td>{{ items.code2 }}</td>
              <td>{{ items.saleroom }}</td>
              <td>{{ items.typeCode }}</td>
              <td>{{ items.deptNo }}</td>
              <td>{{ items.distributor }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Search,
  Cell,
  CellGroup,
  Icon,
  Field,
  DatetimePicker,
  Button,
  Popup
} from "vant";

Vue.use(Search)
  .use(Cell)
  .use(CellGroup)
  .use(Field)
  .use(DatetimePicker)
  .use(Popup)
  .use(Button)
  .use(Icon);

// @ is an alias to /src
import AppHeader from "@/components/AppHeader.vue";

export default {
  name: "Search",
  components: {
    AppHeader
  },

  data() {
    return {
      rangePickerValue: "",
      rangePicker: false,
      rangeSelectedIndex: 0,
      rangeList: [
        "处别",
        "部门",
        "大类代码",
        "中类代码",
        "小类代码",
        "品牌",
        "供应商"
      ],
      searchType: "",
      searchKey: "",
      sortText: "1",
      salesActive: 0,
      startDate: "",
      endDate: "",
      cycleActive: 0,
      orderType: "desc",
      sortActive: 0,
      hasData: false,
      resultData: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.get_defaultData();
      this.get_data();
    });
  },
  methods: {
    rangePickerChange(picker, value) {
      // console.log(picker);
      this.rangeSelectedIndex = value;
      this.rangePickerValue = picker;
      this.searchType = picker;
      this.rangePicker = false;
      this.$nextTick(() => {
        this.get_data();
      });
    },
    searchFun(val) {
      this.$nextTick(() => {
        this.get_data();
      });
    },
    salesTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.sortText = "1";
          this.get_data();
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.sortText = "2";
          this.get_data();
        });
      } else if (name == 2) {
        this.$nextTick(() => {
          this.sortText = "3";
          this.get_data();
        });
      }
    },
    cycleTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.startDate = this.formatDate.pastDate(1, "-");
          this.endDate = this.formatDate.pastDate(1, "-");
          this.get_data();
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.startDate = this.formatDate.pastDate(7, "-");
          this.endDate = this.formatDate.pastDate(1, "-");
          this.get_data();
        });
      } else if (name == 2) {
        this.$nextTick(() => {
          this.startDate = this.formatDate.pastDate(31, "-");
          this.endDate = this.formatDate.pastDate(1, "-");
          this.get_data();
        });
      }
    },
    sortTabClick(name, title, index) {
      if (name == 0) {
        this.$nextTick(() => {
          this.orderType = "desc";
          this.get_data();
        });
      } else if (name == 1) {
        this.$nextTick(() => {
          this.orderType = "asc";
          this.get_data();
        });
      }
    },
    showDatetimePicker() {
      this.datetime_picker_show = true;
    },
    changeFn() {
      this.changeDate = this.currentDate;
    },
    confirmFn(picker, value, index) {
      this.datetime_picker_show = false;
      this.datetime = this.formatDate.formatDate(this.currentDate, "-");
      this.get_data();
    },
    cancelFn() {
      this.datetime_picker_show = false;
    },
    get_defaultData() {
      this.startDate = this.formatDate.pastDate(1, "-");
      this.endDate = this.formatDate.pastDate(1, "-");
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      let params = {
        endDate: this.endDate,
        number: "10",
        orderType: this.orderType,
        searchKey: this.searchKey,
        searchType: this.searchType,
        sortText: this.sortText,
        startDate: this.startDate,
        storeId: this.getStoreInfo.gid
      };
      this.serverHost
        .fetchPost("/sku/saleTopSkuInfo", params)
        .then(res => {
          this.toast.clear();
          // console.log(res);
          const resultData = res.data.data;
          if (resultData.length == false) {
            this.hasData = false;
          } else {
            this.hasData = true;
            this.resultData = resultData;
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    }
  }
};
</script>

<style scoped lang='scss'>
.contrast {
  padding: 2.5px 0;
  justify-content: flex-start;
  margin-left: -10px;
}

.detailed-table {
  padding: 15px;
  .table-content {
    overflow-x: scroll;
    table {
      white-space: nowrap;
      border-collapse: collapse;
      th {
        border: 1px solid #eee;
        padding: 7.5px 15px;
        text-align: center;
        font-size: 14px;
        color: #333;
        font-weight: normal;
        background: #f8f8f8;
      }
      td {
        border-bottom: 1px solid #ececec;
        border: 1px solid #eee;
        padding: 7.5px 15px;
        text-align: center;
        font-size: 13px;
        color: #666;
      }
    }
  }
}
</style>
